<template>
    <div class="wrapper">
        <div class="hd">标题</div>
        <el-input placeholder="请输入内容" v-model="someProps.label"></el-input>
        <div class="hd">ID属性值</div>
        <el-input placeholder="请输入内容" v-model="someProps.id"></el-input>
        <div class="hd">默认值</div>
        <el-input-number v-model="someProps.value"></el-input-number>
    </div>
</template>

<script>
export default {
    name: 'editText',
    props: ['someProps']
}
</script>

